import * as mutation_types from "./mutation_types";
import { getCookie, setCookie } from "@/plugins/Cookies";
import { getCurrentLocalization } from "@/localizations/localizationSettings";

export default {
  [mutation_types.M_CHANGE_LOCALIZATION](state, payload) {
    setCookie("lng=" + payload.selectedLng);
    state.localization[0].defoultLng = payload.selectedLng;
    state.localizationInfo = getCurrentLocalization(getCookie("lng"));
  },
};
