import { getCookie } from "@/plugins/Cookies";

export default {
  checkStateEmailError(state) {
    return state.userInfo[0].actionsEmailError;
  },
  checkLogin(state) {
    const loggedOfLocalStorage = getCookie("UserLogged");
    if (loggedOfLocalStorage != undefined) {
      if (loggedOfLocalStorage) {
        return (state.userInfo[0].logged = loggedOfLocalStorage);
      } else {
        return state.userInfo[0].logged;
      }
    }
  },
  checkUserLogged(state) {
    return state.userInfo[0].logged;
  },
};
