import * as mutation_types from "./mutation_types";
import { getCookie, setCookie, deleteCookie } from "@/plugins/Cookies";

export default {
  [mutation_types.M_REGISTER_NEW_USER](state, payload) {
    let cookiesUsers = JSON.parse(getCookie("Users"));
    const checkEmail = cookiesUsers.find((e) => e.email === payload.email);
    if (checkEmail === undefined) {
      const lastUserId = state.userInfo[0].users.length;
      const newUserId = lastUserId + 1;
      payload.id = newUserId;
      state.userInfo[0].users.push(payload);
      state.userInfo[0].actionsEmailError = false;
      let UsersCookies = JSON.parse(getCookie("Users"));
      UsersCookies.push(payload);
      setCookie("Users=" + JSON.stringify(UsersCookies));
    } else {
      state.userInfo[0].actionsEmailError = true;
    }
  },
  [mutation_types.M_CHECK_LOGIN](state, payload) {
    let UsersCookies = getCookie("Users");
    if (UsersCookies != undefined) {
      const checkLoginPassword = JSON.parse(UsersCookies).find((e) => {
        return e.email === payload.email && e.password === payload.password;
      });
      if (checkLoginPassword !== undefined) {
        state.userInfo[0].logged = true;
        setCookie("UserLogged=true");
      }
    }
  },
  [mutation_types.M_AUTOLOGIN](state) {
    state.userInfo[0].logged = true;
    setCookie("UserLogged=true");
  },
  [mutation_types.M_USERS_IN_COOKI](state) {
    let checkUsersInCookies = getCookie("Users");
    if (checkUsersInCookies === undefined) {
      setCookie("Users=" + JSON.stringify(state.userInfo[0].users));
    }
  },
  [mutation_types.M_LOGOUT](state) {
    state.userInfo[0].logged = false;
    deleteCookie("UserLogged");
  },
};
