import { createRouter, createWebHashHistory } from "vue-router";
import { getCookie } from "@/plugins/Cookies";

const routes = [
  {
    path: "/register",
    name: "Register",
    component: () => import("../views/User/Register.vue"),
    meta: { title: "registration", logged: false },
  },
  {
    path: "/",
    name: "Login",
    component: () => import("../views/User/Login.vue"),
    meta: { title: "authorization", logged: false },
  },
  {
    path: "/persons",
    name: "Perosns",
    component: () => import("../views/OnlyLogged/Persons.vue"),
    meta: { title: "persons", logged: true },
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) document.title = to.meta.title;

  const isAuth = getCookie("UserLogged");
  if (to.meta.logged && !isAuth) {
    next("/");
  } else if (!to.meta.logged && isAuth) {
    next("/persons");
  } else {
    next();
  }
});

export default router;
