import * as types from "./types";
import * as mutation_types from "./mutation_types";

export default {
  [types.A_REGISTER_NEW_USER](context, payload) {
    context.commit(mutation_types.M_REGISTER_NEW_USER, payload);
  },
  [types.A_CHECK_LOGIN](context, payload) {
    context.commit(mutation_types.M_CHECK_LOGIN, payload);
  },
  [types.A_AUTOLOGIN](context) {
    context.commit(mutation_types.M_AUTOLOGIN);
  },
  [types.A_LOGOUT](context) {
    context.commit(mutation_types.M_LOGOUT);
  },
  [types.A_USERS_IN_COOKI](context) {
    context.commit(mutation_types.M_USERS_IN_COOKI);
  },
};
