import { getCookie } from "@/plugins/Cookies";
import { getCurrentLocalization } from "@/localizations/localizationSettings";

export default {
  localizationInfo(state) {
    state.localizationInfo = getCurrentLocalization(
      state.localization[0].defoultLng
    );
    return state.localizationInfo;
  },
  getLocalizationSelected(state) {
    let checkLng = getCookie("lng");
    if (checkLng != undefined) {
      state.localization[0].defoultLng = checkLng;
    }
    return state.localization[0].defoultLng;
  },
};
