import * as types from "./types";
import * as mutation_types from "./mutation_types";

export default {
  [types.A_DELETE_PERSON](context, payload) {
    context.commit(mutation_types.M_DELETE_PERSON, payload);
  },
  [types.A_ADD_NEW_PERSON](context, payload) {
    context.commit(mutation_types.M_ADD_NEW_PERSON, payload);
  },
  [types.A_UPDATE_PERSON](context, payload) {
    context.commit(mutation_types.M_UPDATE_PERSON, payload);
  },
};
