import * as mutation_types from "./mutation_types";
import { getCookie, setCookie } from "@/plugins/Cookies";

export default {
  [mutation_types.M_DELETE_PERSON](state, payload) {
    let personsCookies = JSON.parse(getCookie("Persons"));
    personsCookies = personsCookies.filter(
      (person) => person.id != payload.PersonId
    );
    state.personInfo[0].persons = personsCookies;
    setCookie("Persons=" + JSON.stringify(personsCookies));
  },
  [mutation_types.M_ADD_NEW_PERSON](state, payload) {
    let arrayPersonsInCookies =
      getCookie("Persons") === undefined
        ? []
        : JSON.parse(getCookie("Persons"));
    let lastPersonId = 0;
    if (arrayPersonsInCookies.length != 0) {
      lastPersonId = arrayPersonsInCookies[arrayPersonsInCookies.length - 1].id;
    } else {
      lastPersonId = 0;
    }
    const newPersonId = lastPersonId + 1;
    payload.id = newPersonId;
    arrayPersonsInCookies.push(payload);
    state.personInfo[0].persons = arrayPersonsInCookies;
    setCookie("Persons=" + JSON.stringify(arrayPersonsInCookies));
  },
  [mutation_types.M_UPDATE_PERSON](state, payload) {
    let arrayPersonsInCookies = JSON.parse(getCookie("Persons"));
    let getKeyPerson = arrayPersonsInCookies.findIndex(
      (x) => x.id === payload.id
    );
    arrayPersonsInCookies[getKeyPerson] = payload;
    state.personInfo[0].persons = arrayPersonsInCookies;
    setCookie("Persons=" + JSON.stringify(arrayPersonsInCookies));
  },
};
